@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-width: 600px;
  margin: 0 auto;
  border: 2px solid #353535;
  margin-top: 10px;
  font-family: 'Roboto', sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.player{padding-bottom:20px;padding-top:10px}


.playlist-item{display:flex;flex-direction:row;flex-wrap:nowrap;background-color:#353535;padding:10px;color:white;border-bottom:1px solid #f1f1f1;cursor:pointer}.playlist-item .playlist-item-cover{border:1px solid #f1f1f1;margin-right:20px}.playlist-item .playlist-item-details{display:flex;flex-direction:column}.playlist-item .playlist-item-details .playlist-item-title{font-weight:bold;font-size:18px;margin-bottom:5px}.playlist-item .playlist-item-details .playlist-item-artist{font-size:14px}

